  margin-top: 20px; }

.business-hours {
  grid-area: hours;
  padding-top: 50px; }

.business-hours ul {
  text-align: left;
  margin: 0;
  padding: 0; }

.business-hours li {
  margin-top: 20px; }

.copy-right {
  grid-area: copyright;
  grid-row-start: 4;
  grid-row-end: 6;
  text-align: center;
  padding-top: 30px; }

.copy-right span {
  text-transform: uppercase; }

.footer-logo {
  grid-area: footer-logo;
  object-fit: cover;
  text-align: center;
  padding-top: 50px; }

.footer-content {
  display: grid;
  grid-template-columns: repeat(5, 25%, 15%, 15%, 20%, 25%);
  grid-template-rows: auto;
  grid-template-areas: "test contact  hours footer-logo footer-logo" "copyright copyright copyright copyright copyright";
  margin-bottom: 0px;
  padding: 0px; }

.site-content {
  display: flex;
  line-height: 1.8rem; }

.content-area {
  flex-grow: 1;
  width: 75%; }

.widget-area {
  width: 25%;
  justify-content: center;
  align-items: center;
  padding: 42px 0px 0px 30px;
  line-height: 1.8rem; }

.sidebar-contact li {
  margin-left: 5px;
  line-height: 1.8rem; }

.widget-area ul {
  padding: 0;
  margin: 0;
  list-style: none; }

.day-of-week {
  font-weight: bold; }

.widget a {
  color: #248a83;
  text-decoration: none; }

.widget a:hover {
  color: black;
  text-decoration: none; }

.widget {
  display: inline-block; }

.page-container {
  margin: auto;
  width: 1120px; }

.entry-content h2 {
  text-align: center; }

.blog .site-header {
  border-bottom: 1px solid #e1e1e1; }

.blog .content-area {
  border-right: 1px solid #e1e1e1;
  padding: 50px 60px 50px 0;
  width: 73%; }

.blog .journal-title {
  background-color: #248a83;
  color: #fff;
  font-size: 35px;
  left: 0;
  top: 0;
  padding: 10px;
  position: absolute;
  margin-top: 2rem;
  max-width: 70%;
  display: block;
  line-height: 3rem; }

.blog .journal-header {
  position: relative; }

.blog .journal-meta {
  background-color: #248a83;
  bottom: 0;
  right: 0;
  margin-bottom: .7rem;
  color: #fff;
  display: inline-block;
  font-family: Novecento Sans Web,sans-serif;
  padding: .1rem;
  position: absolute;
  text-transform: uppercase; }

.blog p {
  margin-bottom: 2rem; }

.blog .read-more {
  background-color: transparent;
  color: black;
  border: 1px solid #000;
  font-family: Novecento Sans Web,sans-serif;
  font-size: 15px;
  line-height: 1;
  padding: .75rem 1.5rem;
  text-decoration: none;
  text-transform: uppercase;
  border-radius: 1;
  margin-bottom: 6rem; }

.blog .read-more:hover {
  color: white;
  background-color: #248a83; }

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0; }

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%; }

/* Captions */
.wp-caption {
  margin-bottom: 1.5rem;
  max-width: 100%; }
  .wp-caption img[class*="wp-image-"] {
    display: block;
    margin-left: auto;
    margin-right: auto; }
  .wp-caption .wp-caption-text {
    margin: 0.8075rem 0; }

.wp-caption-text {
  text-align: center; }

/* Galleries*/
.gallery {
  margin-bottom: 1.5rem; }

.gallery-item {
  display: inline-block;
  text-align: center;
  vertical-align: top;
  width: 100%; }
  .gallery-columns-2 .gallery-item {
    max-width: 50%; }
  .gallery-columns-3 .gallery-item {
    max-width: 33.33%; }
  .gallery-columns-4 .gallery-item {
    max-width: 25%; }
  .gallery-columns-5 .gallery-item {
    max-width: 20%; }
  .gallery-columns-6 .gallery-item {
    max-width: 16.66%; }
  .gallery-columns-7 .gallery-item {
    max-width: 14.28%; }
  .gallery-columns-8 .gallery-item {
    max-width: 12.5%; }
  .gallery-columns-9 .gallery-item {
    max-width: 11.11%; }

.gallery-caption {
  display: block; }

